@import "../../assets/styles/variables";
@import "../../assets/styles/mixins";

.product-page {
    // .product-page__body-top
    &__body-top {
        display: flex;
        @include adaptive-value(margin-bottom, 40, 20, 1);
        @media (max-width: 992px) {
            flex-wrap: wrap;
        }
    }
    // .product-page__sliders-body
    &__sliders-body {
        flex: 0 1 530px;
        min-width: 0;
        margin-right: 20px;
        @media (max-width: 992px) {
            margin: 0 auto;
        }
    }
    // .product-page__content
    &__content {
        flex: 0 1 750px;
        @media (max-width: 992px) {
            flex: 1 1 750px;
        }
    }
    // .product-page__content-box
    &__content-box {
        border: 1px solid #eaeaf0;
        border-radius: 10px;
        @include adaptive-value(padding, 30, 10, 1);
        margin-bottom: 30px;
    }
    // .product-page__title
    &__title {
        @include adaptive-font(36, 24, 0);
        @include adaptive-value(margin-bottom, 30, 15, 1);
    }
    // .product-page__actions
    &__actions {
        margin-bottom: 20px;
    }
}
.user-actions {
    display: flex;
    align-items: center;
    justify-content: space-between;
    @media (max-width: 450px) {
        flex-wrap: wrap;
    }
    // .user-actions__evaluation
    &__evaluation {
        display: flex;
        align-items: baseline;
        margin-right: 20px;
        @media (max-width: 450px) {
            margin-bottom: 20px;
        }
    }
    // .user-actions__rating
    &__rating {
        margin-right: 30px;
        font-size: 32px;
        .rating__value {
            width: 31px;
        }
    }
    // .user-actions__reviews
    &__reviews {
        display: flex;
        align-items: center;
        column-gap: 5px;
        font-weight: 500;
        line-height: 100%;
        color: #070c11;
        transition: color 0.3s;
        @media (any-hover: hover) {
            &:hover {
                color: #4878a6;
            }
        }
        &:focus-visible {
            outline: 2px solid #2a5275;
            color: #4878a6;
        }
        @media (max-width: 400px) {
            font-size: 14px;
        }
    }
    // .user-actions__btn
    &__btn {
        display: inline-flex;
        align-items: center;
        justify-content: center;
        border: 1px solid #c8cacb;
        border-radius: 4px;
        width: 48px;
        height: 48px;
        background-color: transparent;
        transition: all 0.3s;
        @media (any-hover: hover) {
            &:hover {
                background-color: #dae1e6;
            }
        }
        &:focus-visible {
            outline: 2px solid #2a5275;
            outline-offset: 2px;
            border-radius: 4px;
            transition: none;
        }
        &:not(:last-child) {
            margin-right: 5px;
        }
        // .user-actions__btn--favorite
        &--favorite {
            background: url(../../assets/images/icons/liked.svg) center no-repeat;
            &.in-favorites {
                background: url(../../assets/images/icons/products/favourite-added.svg) center no-repeat;
                @media (any-hover: hover) {
                    &:hover {
                        background: url(../../assets/images/icons/products/favourite-added.svg) center no-repeat,
                            #dae1e6;
                    }
                }
            }
        }
        // .user-actions__btn--compare
        &--compare {
            background: url(../../assets/images/icons/compare.svg) center no-repeat;
            &.in-compare {
                background: url(../../assets/images/icons/products/compare-added.svg) center no-repeat;
                @media (any-hover: hover) {
                    &:hover {
                        background: url(../../assets/images/icons/products/compare-added.svg) center no-repeat, #dae1e6;
                    }
                }
            }
        }
    }
}
.purchase-product {
    display: flex;
    align-items: center;
    @media (max-width: 768px) {
        flex-wrap: wrap;
        gap: 10px;
    }
    // .purchase-product__item
    &__item {
        flex: 1 1 250px;
        margin-right: 10px;
        @media (max-width: 768px) {
            margin-right: 0;
            margin-bottom: 20px;
        }
    }
    // .purchase-product__old-price
    &__old-price {
        font-size: 16px;
        font-weight: 700;
        line-height: 130%;
        text-decoration: line-through;
        color: #838688;
        &::before {
            content: "$ ";
        }
        &:not(:last-child) {
            margin-right: 10px;
        }
    }
    // .purchase-product__discount
    &__discount {
        display: inline-block;
        background-color: #f37748;
        border-radius: 14px;
        padding: 3px 8px;
        font-size: 12px;
        font-weight: 700;
        white-space: nowrap;
        color: #ffffff;
    }
    // .purchase-product__dicsount-percent
    &__dicsount-percent {
        display: inline-block;
        margin-right: 3px;
        padding: 2px 4px;
        background-color: #fff;
        border-radius: 14px;
        color: #070c11;
        font-size: 14px;
        font-weight: 400;
    }
    // .purchase-product__current-price
    &__current-price {
        font-weight: 700;
        font-size: 24px;
        line-height: 130%;
        color: #070c11;
        margin-bottom: 5px;
        &::before {
            content: "$ ";
        }
    }
    // .purchase-product__button
    &__button {
        display: inline-flex;
        flex: 1 1 200px;
        padding: 14px 15px;
        justify-content: center;
        font-weight: 700;
        line-height: 130%;
        white-space: nowrap;
        border-radius: 4px;
        transition: all 0.5s;
        &:not(:last-child) {
            margin-right: 10px;
            @media (max-width: 768px) {
                margin-right: 0;
            }
        }
        @media (any-hover: hover) {
            &:hover {
                border: 1px solid #4878a6;
                background-color: #2a5275;
                color: #ffffff;
            }
        }
        &:focus-visible {
            outline: 2px solid #2a5275;
            outline-offset: 2px;
            border-radius: 4px;
            transition: none;
        }
        // .purchase-product__button--to-cart
        &--to-cart {
            flex: 0 0 200px;
            font-size: 16px;
            font-weight: 700;
            color: #ffffff;
            padding-right: 40px;
            padding-left: 40px;
            background-color: #4878a6;
            border: 1px solid #4878a6;
            @media (max-width: 768px) {
                flex: 1 1 200px;
            }
        }
        // .purchase-product__button--in-cart
        &--in-cart {
            flex: 0 0 200px;
            position: relative;
            padding-right: 40px;
            font-size: 16px;
            font-weight: 700;
            color: #4878a6;
            border: 1px solid transparent;
            background: #edf2f6;
            @media (max-width: 768px) {
                flex: 1 1 200px;
            }
            &::after {
                position: absolute;
                content: "";
                top: 50%;
                right: 10px;
                transform: translateY(-50%);
                background-image: url("../../assets/images/icons/products/cart-added.svg");
                background-repeat: no-repeat;
                background-size: cover;
                width: 21px;
                height: 21px;
            }
            @media (any-hover: hover) {
                &:hover {
                    background-color: #dae1e6;
                    color: #4878a6;
                    border: 1px solid transparent;
                }
            }
        }
        // .purchase-product__button--one-click
        &--one-click {
            color: #4878a6;
            border: 1px solid #4878a6;
        }
    }
}
.shipping-product {
    display: flex;
    column-gap: 20px;
    @media (max-width: 768px) {
        flex-wrap: wrap;
        column-gap: unset;
    }
    // .shipping-product__item
    &__item {
        flex: 1 1 365px;
        padding-left: 35px;
        padding-top: 4px;
        background-repeat: no-repeat;
        background-position: top left;
        background-size: 24px;
        @media (max-width: 768px) {
            &:not(:last-child) {
                margin-bottom: 20px;
            }
        }
        // .shipping-product__item--delivery
        &--delivery {
            background-image: url(../../assets/images/icons/products/shipping.svg);
        }
        // .shipping-product__item--payment
        &--payment {
            background-image: url(../../assets/images/icons/products/purse.svg);
        }
    }
    // .shipping-product__title
    &__title {
        font-weight: 700;
        color: #070c11;
        margin-bottom: 10px;
    }
    // .shipping-product__text
    &__text {
        line-height: 120%;
        @media (max-width: 450px) {
            margin-left: -33px;
        }
    }
}
.product-tabs {
    @include adaptive-value(padding-bottom, 80, 30, 1);
    // .product-tabs__header
    &__header {
        @include adaptive-value(margin-bottom, 40, 30, 1);
        background-color: #edf2f6;
        @media (max-width: 768px) {
            margin-left: -10px;
            margin-right: -10px;
        }
    }
    // .product-tabs__header-list
    &__header-list {
        display: flex;
        padding: 0 20px;
        border-radius: 8px;
        overflow-x: auto;
    }
    // .product-tabs__header-item
    &__header-item {
        &:not(:last-child) {
            margin-right: 20px;
        }

        a {
            display: inline-block;
            font-weight: 700;
            color: #070c11;
            line-height: 130%;
            white-space: nowrap;
            border-bottom: 4px solid transparent;
            padding: 25px 20px;
            cursor: pointer;
            transition: color 0.3s, background-color 0.3s, border-bottom 0.3s;

            &.active {
                color: #2a5275;
                border-bottom: 4px solid #2a5275;
            }

            @media (any-hover: hover) {
                &:hover {
                    background-color: #dae4ed;
                    transition: background-color 0.3s;
                }
            }
            &:focus-visible {
                outline: 2px solid #2a5275;
                outline-offset: -2px;
                background-color: #dae4ed;
                transition: none;
            }
            @media (max-width: 450px) {
                padding: 15px 20px;
            }
        }
    }
}
