@keyframes pulse {
    0% {
        opacity: 1;
    }
    50% {
        opacity: 0.6;
    }
    100% {
        opacity: 1;
    }
}

.product-page-skeleton {
    &__breadcrumbs {
        height: 16px;
        max-width: 60%;
        margin-top: calc(20px + 20*(100vw - 320px)/1010);
        margin-bottom: calc(20px + 20*(100vw - 320px)/1010);
        border-radius: 4px;
        background-color: rgba(0, 0, 0, 0.11);
        animation: pulse 1.5s ease-in-out 0.5s infinite;
        transition: opacity .3s;
    }
    &__product {

    }
    &__body-top {
        display: flex;
        column-gap: 20px;
        @media(max-width: 992px) {
            flex-wrap: wrap;
            flex-direction: column;
        }
    }
    &__slider {
        flex: 0 1 40%;
        @media(max-width: 992px) {
            flex: 1 1 auto;
            margin-bottom: 40px;
        }
    }
    &__slider-photo {
        position: relative;
        width: 100%;
        height: 100%;
        max-height: 330px;
        @media(max-width: 992px) {
            max-width: unset;
        }
    }
    &__slider-img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        padding-bottom: 58%;
        border-radius: 15px;
        background-color: rgba(0, 0, 0, 0.11);
        animation: pulse 1.5s ease-in-out 0.5s infinite;
        transition: opacity .3s;
        @media(max-width: 992px) {
            max-width: 500px;
            margin: 0 auto 20px;
            position: static;
            padding-bottom: unset;
            height: 300px;
        }
        @media(max-width: 400px) {
            height: 250px;
        }
    }
    &__slider-controls {
        height: 40px;
        width: 80%;
        margin: 0 auto;
        border-radius: 4px;
        background-color: rgba(0, 0, 0, 0.11);
        animation: pulse 1.5s ease-in-out 0.5s infinite;
        transition: opacity .3s;
        @media(max-width: 992px) {
            width: 250px;
            height: 50px;
        }
    }
    &__content {
        flex: 1 0 auto;
    }
    &__title {
        height: 20px; 
        border-radius: 4px;
        background-color: rgba(0, 0, 0, 0.11);
        animation: pulse 1.5s ease-in-out 0.5s infinite;
        transition: opacity .3s;
        &:not(:last-child) {
            margin-bottom: 10px;
        }
        &:nth-child(2) {
            max-width: 80%;
            margin-bottom: 40px;
        }
    }
    &__content-box {
        padding: 30px;
        border: 1px solid #eaeaf0;
        border-radius: 10px;
        @media(max-width: 450px) {
            padding: 10px;
        }
    }
    &__actions {
        
    }
    &__evaluation {
        max-width: 40%;
        margin-bottom: 20px;
        height: 30px;
        border-radius: 4px;
        background-color: rgba(0, 0, 0, 0.11);
        animation: pulse 1.5s ease-in-out 0.5s infinite;
        transition: opacity .3s;
    }
    &__btns {
        display: flex;
        justify-content: space-between;
        align-items: center;
        gap: 20px;
        @media(max-width: 450px) {
            flex-direction: column;
            gap: 10px;
        }
    }
    &__btn {
        flex: 0 1 32%;
        height: 50px;
        border-radius: 4px;
        background-color: rgba(0, 0, 0, 0.11);
        animation: pulse 1.5s ease-in-out 0.5s infinite;
        transition: opacity .3s;
        &:first-child {
            height: 60px;
            @media(max-width: 450px) {
                width: 60%;
                margin-right: auto;
            }
        }
        @media(max-width: 450px) {
            flex: 1 1 auto;
            height: 40px;
            width: 100%;
        }
    }
    &__shipping {
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 30px 0;
        gap: 20px;
    }
    &__shipping-item {
        height: 80px;
        flex: 0 1 50%;
        border-radius: 4px;
        background-color: rgba(0, 0, 0, 0.11);
        animation: pulse 1.5s ease-in-out 0.5s infinite;
        transition: opacity .3s;
    }
    &__body-bottom {

    }
    &__tabs {
    }
    &__tabs-header {
        max-width: 60%;
        height: 75px;
        margin-bottom: 30px;
        border-radius: 4px;
        background-color: rgba(0, 0, 0, 0.11);
        animation: pulse 1.5s ease-in-out 0.5s infinite;
        transition: opacity .3s;
    }
    &__tabs-body {
        margin-bottom: 60px;
       
    }
    &__tabs-text {
        height: 70px;
        margin-bottom: 10px;
        border-radius: 4px;
        background-color: rgba(0, 0, 0, 0.11);
        animation: pulse 1.5s ease-in-out 0.5s infinite;
        transition: opacity .3s;
        &:first-child {
            height: 40px;
        }
    }
}