@import "../../assets/styles/variables";
@import "../../assets/styles/mixins";

.product-tab {
    display: block;
    transition: all .3s;
    &:not(:last-child) {
        max-width: 975px;  
    } 
    
    // .product-tab__title
    &__title {
        font-weight: 700;
        @include adaptive-font(28, 20, 0);
        line-height: 120%;
        color: #070C11;
        @include adaptive-value(margin-bottom, 30, 20, 1);
    }
    // .product-tab__text
    &__text {
        &:not(:last-child) {
            margin-bottom: 5px;
        }
    }
    // .product-tab__characteristics
    &__characteristics {
        padding: 0 20px;
        max-width: 975px;
    }
    // .product-tab__characteristic
    &__characteristic {
        display: flex;
        align-items: center;
        min-height: 72px;
        &:not(:last-child) {
            border-bottom: 1px solid #C8CACB;
        }
        @media (max-width: 600px) {
            flex-wrap: wrap;
            padding: 20px 0;
        }
    }
    // .product-tab__characteristic-title
    &__characteristic-title {
        flex: 0 0 50%;
        font-weight: 700;
        line-height: 130%;
        padding-right: 20px;
        @media (max-width: 600px) {
            flex: 1 1 100%;
        }
    }
    // .product-tab__characteristic-value
    &__characteristic-value {
        flex: 0 0 50%;
        @media (max-width: 600px) {
            flex: 1 1 100%;
        }
    }
}