@import "../../assets/styles/variables";
@import "../../assets/styles/mixins";

.product-tab {
    display: block;
    transition: all .3s;

    // .product-tab__title
    &__title {
        font-weight: 700;
        @include adaptive-font(28, 20, 0);
        line-height: 120%;
        color: #070C11;
        @include adaptive-value(margin-bottom, 30, 20, 1);
    }
    // .product-tab__text
    &__text {
        &:not(:last-child) {
            margin-bottom: 5px;
        }
    }
    // .product-tab__review
    &__review {
        display: flex;
        width: 100%;
        @media (max-width: 900px) {
            flex-wrap: wrap;
        }
    }
    // .product-tab__review-content
    &__review-content, &__review-appsent {
        flex: 0 1 640px;
        &:not(:last-child) {
            margin-right: 20px;
        }
        @media (max-width: 900px) {
            flex: 1 1 640px;
            &:not(:last-child) {
                margin-right: 0;
                margin-bottom: 30px;
            }
        }
    }
    // .product-tab__review-actions
    &__review-actions {
        flex: 0 1 340px;
        margin-left: auto;
        margin-right: auto;
        @media (max-width: 900px) {
            flex-wrap: wrap;
            margin-left: 0;
            margin-right: 0;
        }
    }
}
.content-reviews {
    // .content-reviews__item
    &__item {
        &:not(:last-child) {
            margin-bottom: 20px;
        }
    }
}
.content-review {
    @include adaptive-value(padding, 40, 10, 1);
    background-color: #EDF2F6;
    border-radius: 10px;
    // .content-review__header
    &__header {
        display: flex;
        align-items: center;
        margin-bottom: 20px;
        @media (max-width: 600px) {
            flex-wrap: wrap;
        }
    }
    // .content-review__header-img
    &__header-img {
        display: inline-flex;
        align-items: center;
        justify-content: center;
        flex: 0 0 50px;
        background-color: lightgray;
        font-weight: 900;
        font-size: 20px;
        height: 50px;
        border-radius: 50%;
        margin-right: 20px;
        @media (max-width: 400px) {
            margin-right: 10px;
        }
    }
    // .content-review__header-username
    &__header-username {
        font-weight: 700;
        line-height: 120%;
        color: #070C11;
        margin-right: 20px;
        @media (max-width: 600px) {
            margin-right: 10px;
        }
    }
    // .content-review__header-date
    &__header-date {
        line-height: 120%;
        color: #838688;
        white-space: nowrap;
        margin-right: 30px;
    }
    // .content-review__header-rating
    &__header-rating {
        font-size: 32px;
        .rating__value {
            color: #838688;
            font-weight: 700;
            line-height: 120%;
        }
    }
    // .content-review__body-title
    &__body-title {
        font-weight: 700;
        line-height: 120%;
        color: #070C11;
        margin-bottom: 10px;
    }
}
.actions-review {
    // .actions-review__title
    &__title {
        font-weight: 700;
        line-height: 120%;
        color: #070C11;
        margin-bottom: 10px;
    }
    // .actions-review__text
    &__text {
        margin-bottom: 20px;
    }
    // .actions-review__btn
    &__btn {
        padding: 14px 95px;
        color: #ffffff;
        line-height: 130%;
        white-space: nowrap;
        background-color: #4878A6;
        border: 1px solid #4878A6;
        border-radius: 4px;
        transition: all .3s;
        @media (any-hover: hover) {
            &:hover {
                background-color: #2a5275;
            }
        }
        @media (max-width: 600px) {
            @include adaptive-value(padding-left, 95, 50, 1);
            @include adaptive-value(padding-right, 95, 50, 1);
        }
    }
}