.page404 {
    text-align: center;
    padding: 20px 0;
    flex: 0 1 auto;
    min-height: 50vh;
    display: flex;
    align-items: center;
    justify-content: center;
    align-items: center;
    &__title {
        font-size: 30px;
        font-weight: 700;
        text-transform: uppercase;
        margin-bottom: 20px;
    }
    &__description {
        margin-bottom: 20px;
    }
    &__btn {
        display: inline-block;
        font-weight: 500;
        color: #fff;
        padding: 16px 26px;
        background-color: #2a5275;
        border-radius: 4px;
        transition: background-color 0.5s;
        @media (any-hover: hover) {
            &:hover {
                background-color: #4878a6;
            }
        }
    }
}