.slider-thumbs {
    max-width: 290px;
    margin: 0 auto;
    position: relative;
    padding: 10px 0;
    // .slider-thumbs__swiper
    &__swiper {
        position: static;
        max-width: 200px;
        padding: 0 10px;
    }
    // .slider-thumbs__wrapper
    &__wrapper {
    }
    // .slider-thumbs__slide
    &__slide {
        max-width: 56px;
        max-height: 56px;
        border-radius: 5px;
        border: 2px solid transparent;
        cursor: pointer;
        &.swiper-slide-thumb-active {
            border: 2px solid #4878a6;
        }
        // &:focus-visible {
        //     outline: 2px solid #2a5275;
        //     outline-offset: -2px;
        //     border-radius: 4px;
        //     transition: none;
        // }
    }
    // .slider-thumbs__image
    &__image {
        max-width: 100%;
        object-fit: cover;
    }
}
// .swiper-button-prev, swiper-button-next
.swiper-button-prev,
.swiper-button-next {
    position: absolute;
    z-index:unset;
    background: url(../../assets/images/icons/sliders/arrow.svg) no-repeat center, #edf2f6;
    width: 48px;
    height: 48px;
    border-radius: 50%;
    cursor: pointer;
    transition: all 0.3s;
    &::after {
        display: none;
    }
    @media (any-hover: hover) {
        &:hover {
            background: url(../../assets/images/icons/sliders/arrow.svg) no-repeat center, #dae4ed;
        }
    }
    @media(max-width: 450px) {
        display: none;
    }

    &.swiper-button-disabled {
        border: 1px solid gray;
        opacity: 0.3;
        cursor: unset;
    }
}
    // .swiper-button-prev
    .swiper-button-prev {
        left: -30px;
        transform: rotate(-180deg);
    }

    // .swiper-button-next
    .swiper-button-next {
        right: -30px;
    }
